/* eslint-disable */

$(".platform-leaderboard__video__iframe").fitVids();

// Product Page Video Play CTA Button Interaction
$('.platform-leaderboard__video__play-cta').on('click', function() {
  $('.platform-leaderboard__video__iframe').css('visibility', 'visible').addClass('js-video-playing');
});

$(function() {
    var player = $('#vim1');
    var playerOrigin = '*';
    //var status = $('.status');

    // Listen for messages from the player
    if (window.addEventListener) {
        window.addEventListener('message', onMessageReceived, false);
    }
    else {
        window.attachEvent('onmessage', onMessageReceived, false);
    }

    // Handle messages received from the player
    function onMessageReceived(event) {
        // Handle messages from the vimeo player only
        if (!(/^https?:\/\/player.vimeo.com/).test(event.origin)) {
            return false;
        }

        if (playerOrigin === '*') {
            playerOrigin = event.origin;
        }

        var data = JSON.parse(event.data);

        switch (data.event) {
            case 'ready':
                onReady();
                break;

            case 'playProgress':
                onPlayProgress(data.data);
                break;

            case 'pause':
                onPause();
                break;

            case 'finish':
                onFinish();
                break;
        }
    }

    // Call the API when a button is pressed
    $('.platform-leaderboard__video__play-cta').on('click', function() {
        post('play');
    });

    // Helper function for sending a message to the player
    function post(action, value) {
        var data = {
          method: action
        };

        if (value) {
            data.value = value;
        }

        var message = JSON.stringify(data);
        player[0].contentWindow.postMessage(data, playerOrigin);
    }

    function onReady() {
        post('addEventListener', 'pause');
        post('addEventListener', 'finish');
        post('addEventListener', 'playProgress');
    }

    function onPause() {
    }

    function onFinish() {
        $('.platform-leaderboard__video__iframe').css('visibility', 'hidden').removeClass('js-video-playing');
    }

    function onPlayProgress(data) {
    }
});



